import { useCallback } from 'react';
import { chatService } from '@/services/api';
import { useChatStore } from '@/store/chatStore';
import { ChatMessage, ChatOptions, OriginalChatMessage, convertFromHuihuaFormat } from '@/types';

export const useChat = () => {
  const {
    currentAgent,
    messages,
    preferences,
    currentSession,
    addMessage,
    updateLastMessage,
    setIsStreaming,
    setStreamingStatus,
    createNewSession,  // æ–°çš„åˆ›å»ºä¼šè¯æ–¹æ³•
  } = useChatStore();

  const sendMessage = useCallback(async (
    content: string,
    options?: ChatOptions
  ) => {
    if (!currentAgent) {
      throw new Error('æ²¡æœ‰é€‰æ‹©æ™ºèƒ½ä½“');
    }

    // å¦‚æœæ²¡æœ‰å½“å‰ä¼šè¯ï¼Œåˆ›å»ºä¸€ä¸ªï¼ˆhuihua.md è¦æ±‚ï¼‰
    if (!currentSession) {
      createNewSession();
    }

    // æ·»åŠ ç”¨æˆ·æ¶ˆæ¯ï¼ˆæŒ‰ huihua.md æ ¼å¼ï¼‰
    const userMessage: ChatMessage = {
      HUMAN: content
    };
    addMessage(userMessage);

    // åˆ›å»ºåŠ©æ‰‹æ¶ˆæ¯å ä½ç¬¦
    const assistantMessage: ChatMessage = {
      AI: ''
    };
    addMessage(assistantMessage);

    // è¯»å–ç”¨äº chatId çš„ä¼šè¯ idï¼ˆé¦–æ¡æ¶ˆæ¯å·²åˆ›å»ºä¼šè¯æ—¶éœ€è¦ä»æœ€æ–°çŠ¶æ€è·å–ï¼‰
    let sessionIdForChat: string | undefined;
    if (!currentSession) {
      const { currentSession: latestSession } = useChatStore.getState();
      sessionIdForChat = latestSession?.id;
    } else {
      sessionIdForChat = currentSession.id;
    }

    // ä»…å‘é€æœ¬æ¬¡è¾“å…¥çš„æ¶ˆæ¯ï¼Œä¸æ‰“åŒ…å†å²æ¶ˆæ¯
    const chatMessages: OriginalChatMessage[] = convertFromHuihuaFormat([userMessage]);

    // é€ä¼  chatId åˆ°åç«¯ï¼ˆä»¥ä¼šè¯ id ä½œä¸º chatIdï¼‰ï¼Œä¿ç•™å…¶ä»– options
    const mergedOptions: ChatOptions | undefined = sessionIdForChat
      ? { ...options, chatId: sessionIdForChat }
      : options;
    
    try {
      setIsStreaming(true);
      
      if (preferences.streamingEnabled) {
        // æµå¼å“åº”
        await chatService.sendStreamMessage(
          currentAgent.id,
          chatMessages,
          (chunk) => {
            updateLastMessage(chunk);
          },
          (status) => {
            setStreamingStatus(status);
          },
          mergedOptions,
          // onInteractive: æ”¶åˆ°äº¤äº’èŠ‚ç‚¹åï¼Œæ–°å¢ä¸€æ¡äº¤äº’æ°”æ³¡
          (interactiveData) => {
            try {
              // ç›´æ¥ä»¥äº¤äº’æ•°æ®ä½œä¸ºä¸€æ¡æ–°æ¶ˆæ¯æ¸²æŸ“
              addMessage({ interactive: interactiveData });
            } catch (e) {
              console.warn('å¤„ç† interactive äº‹ä»¶å¤±è´¥:', e, interactiveData);
            }
          },
          // onChatId: å¯é€‰è®°å½•
          (cid) => {
            console.log('æ”¶åˆ° chatId äº‹ä»¶:', cid);
          }
        );
      } else {
        // éæµå¼å“åº”
        const response = await chatService.sendMessage(
          currentAgent.id,
          chatMessages,
          mergedOptions
        );
        
        const assistantContent = response.choices[0]?.message?.content || '';
        updateLastMessage(assistantContent);
      }
      
    } catch (error) {
      console.error('å‘é€æ¶ˆæ¯å¤±è´¥:', error);
      updateLastMessage('æŠ±æ­‰ï¼Œå‘é€æ¶ˆæ¯æ—¶å‡ºç°é”™è¯¯ã€‚è¯·ç¨åé‡è¯•ã€‚');
    } finally {
      setIsStreaming(false);
      setStreamingStatus(null);
    }
  }, [
    currentAgent,
    messages,
    preferences.streamingEnabled,
    currentSession,
    addMessage,
    updateLastMessage,
    setIsStreaming,
    setStreamingStatus,
    createNewSession,
  ]);

  // ç»§ç»­è¿è¡Œï¼šäº¤äº’èŠ‚ç‚¹-ç”¨æˆ·é€‰æ‹©
  const continueInteractiveSelect = useCallback(async (value: string) => {
    await sendMessage(value);
  }, [sendMessage]);

  // ç»§ç»­è¿è¡Œï¼šäº¤äº’èŠ‚ç‚¹-è¡¨å•è¾“å…¥
  const continueInteractiveForm = useCallback(async (values: Record<string, any>) => {
    const content = JSON.stringify(values);
    await sendMessage(content);
  }, [sendMessage]);

  return {
    sendMessage,
    continueInteractiveSelect,
    continueInteractiveForm,
  };
};