import React, { useEffect, useRef, memo } from 'react';
import { ChatMessage } from '@/types';
import { MessageItem } from './MessageItem';
// å·²ç§»é™¤ FastGPTStatusIndicator å¯¼å…¥ï¼Œæ–¹æ¡ˆAæœ€å°åŒ–ï¼šä»…å»æ‰è¯¥UIå—
// import { FastGPTStatusIndicator } from './FastGPTStatusIndicator';
import { useChatStore } from '@/store/chatStore';

interface MessageListProps {
  messages: ChatMessage[];
  isStreaming?: boolean;
  onInteractiveSelect?: (value: string) => void;
  onInteractiveFormSubmit?: (values: Record<string, any>) => void;
}

export const MessageList: React.FC<MessageListProps> = memo(({
  messages,
  isStreaming = false,
  onInteractiveSelect,
  onInteractiveFormSubmit
}) => {
  const scrollRef = useRef<HTMLDivElement>(null);
  const lastMessageRef = useRef<HTMLDivElement>(null);
  const { currentAgent, streamingStatus } = useChatStore();

  // è‡ªåŠ¨æ»šåŠ¨åˆ°åº•éƒ¨
  useEffect(() => {
    if (lastMessageRef.current) {
      lastMessageRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, [messages, isStreaming]);

  return (
    <div 
      ref={scrollRef}
      className="h-full overflow-y-auto bg-gray-50 dark:bg-gray-900"
    >
      <div className="max-w-4xl mx-auto px-4 py-6">
        <div className="space-y-6">
          {messages.map((message, index) => {
            // huihua.md æ ¼å¼æ²¡æœ‰ idï¼Œä½¿ç”¨ index ä½œä¸º key
            const isLastMessage = index === messages.length - 1;
            const isAssistantMessage = message.AI !== undefined;
            
            return (
              <div key={index}>
                <MessageItem
                  message={message}
                  isStreaming={isStreaming && isLastMessage && isAssistantMessage}
                  currentAgent={currentAgent ?? undefined}
                  streamingStatus={streamingStatus ?? undefined}
                  onInteractiveSelect={onInteractiveSelect}
                  onInteractiveFormSubmit={onInteractiveFormSubmit}
                />
                {/* æœ€åä¸€ä¸ªæ¶ˆæ¯çš„å ä½å…ƒç´  */}
                {isLastMessage && (
                  <div ref={lastMessageRef} className="h-1" />
                )}
              </div>
            );
          })}
          
          {/* FastGPT ç‰¹æœ‰çŠ¶æ€æ˜¾ç¤º - å·²è¿ç§»åˆ°æ°”æ³¡å†…éƒ¨ï¼Œç§»é™¤æ­¤å¤„æ¸²æŸ“ */}
          {/* æ ‡å‡†æµå¼ä¼ è¾“æŒ‡ç¤ºå™¨ï¼ˆé FastGPTï¼‰ */}
          {isStreaming && (!currentAgent || currentAgent.provider !== 'fastgpt') && (
            <div className="flex justify-start">
              <div className="flex items-center space-x-2 px-4 py-2 bg-white dark:bg-gray-800 rounded-lg shadow-sm">
                <div className="flex space-x-1">
                  <div className="w-2 h-2 bg-blue-500 rounded-full animate-pulse"></div>
                  <div className="w-2 h-2 bg-blue-500 rounded-full animate-pulse" style={{ animationDelay: '0.2s' }}></div>
                  <div className="w-2 h-2 bg-blue-500 rounded-full animate-pulse" style={{ animationDelay: '0.4s' }}></div>
                </div>
                <span className="text-sm text-gray-500 dark:text-gray-400">æ­£åœ¨ç”Ÿæˆå›ç­”...</span>
                {/* åœ¨ä¸‰ç‚¹åŠ¨ç”»åå±•ç¤º flowNodeStatus æ•°æ®ï¼ˆè‹¥å­˜åœ¨ï¼‰ */}
                
                {streamingStatus?.type === 'flowNodeStatus' && (
                  <span className="text-xs text-gray-400 dark:text-gray-500 ml-3">
                    {streamingStatus.moduleName || 'æœªçŸ¥æ¨¡å—'} - {streamingStatus.status || 'running'}
                  </span>
                )}
                
              </div>
            </div>
          )}
          
          {/* åº•éƒ¨ç•™ç™½ */}
          <div className="h-20" />
        </div>
      </div>
    </div>
  );
});