import { useState, useCallback, useRef } from 'react';
import { agentService } from '@/services/api';
import { useChatStore } from '@/store/chatStore';

export const useAgents = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const abortControllerRef = useRef<AbortController | null>(null);
  
  const { 
    setAgents, 
    setAgentsLoading, 
    setAgentsError,
    setCurrentAgent,
    currentAgent 
  } = useChatStore();

  const fetchAgents = useCallback(async () => {
    // å–æ¶ˆä¸Šä¸€æ¬¡è¯·æ±‚
    if (abortControllerRef.current) {
      abortControllerRef.current.abort();
    }
    
    abortControllerRef.current = new AbortController();
    
    setLoading(true);
    setAgentsLoading(true);
    setError(null);
    setAgentsError(null);

    try {
      const agents = await agentService.getAgents();
      
      // æ£€æŸ¥è¯·æ±‚æ˜¯å¦è¢«å–æ¶ˆ
      if (abortControllerRef.current?.signal.aborted) {
        return;
      }
      
      setAgents(agents);
      
      // å¦‚æœæ²¡æœ‰å½“å‰æ™ºèƒ½ä½“ï¼Œè‡ªåŠ¨é€‰æ‹©ç¬¬ä¸€ä¸ªå¯ç”¨çš„
      if (!currentAgent && agents.length > 0) {
        const firstActive = agents.find(agent => agent.status === 'active') || agents[0];
        if (firstActive) {
          setCurrentAgent(firstActive);
        }
      }
    } catch (err) {
      // å¦‚æœæ˜¯å–æ¶ˆæ“ä½œï¼Œä¸å¤„ç†é”™è¯¯
      if (err instanceof Error && err.name === 'AbortError') {
        return;
      }
      
      const errorMessage = err instanceof Error ? err.message : 'è·å–æ™ºèƒ½ä½“åˆ—è¡¨å¤±è´¥';
      setError(errorMessage);
      setAgentsError(errorMessage);
    } finally {
      setLoading(false);
      setAgentsLoading(false);
    }
  }, [currentAgent, setAgents, setAgentsLoading, setAgentsError, setCurrentAgent]);

  return {
    loading,
    error,
    fetchAgents,
  };
};